import React,{Component} from 'react';
import PropTypes from 'prop-types';

class Sct1_1_bg extends Component{
    constructor(props){
        super(props);
    }
    render(){
        var one;
        if (this.props.bool) {
            one = 'bg';
        }
        return (
            <div className={one} style={{
                backgroundImage: `url('/ReactBlog/${this.props.source}')`,
            }}>
            </div>
        );
    }
}

Sct1_1_bg.propTypes = {
    source: PropTypes.string,
    bool: PropTypes.bool
};

export default Sct1_1_bg;